* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}

.logo {
  display: flex;
  align-items: center;
}

#logo {
  margin-right: 20px;
  height: 60px;
}

#root {
  width: 100%;
}

table {
  width: 100%;
}

th,
td {
  border-bottom: 1px solid;
  padding: 5px;
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 150px;
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.385);
}

.buttons {
  width: 100%;
  display: flex;
}

.navH1 {
  font-size: clamp(10px, 2vw, 50px);
}

.links ul {
  display: flex;
}

.links ul a {
  text-decoration: none;
  color: black;
}

#output {
  margin-top: 20px;

}

#output p {
  border-radius: 5px;
  padding: 5px;
  margin: 5px;
}

.homeBase {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.links ul li {
  padding: 0 10px;
  font-size: 2rem;
  list-style: none;
}

.ausgabe {
  display: flex;
  align-items: flex-start;
  width: 100vw;
}

.selector {
  margin: 15px;
  width: 50%;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

}

#news {
  padding: 10px 30px;
  width: 75%;
  margin: 5px;
  border-radius: 5px;
}


.newsi h2 {
  font-size: 20px;
}

.newsi {
  padding: 10px 30px;
  box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.466);
  width: 100%;
  margin: 5px;
  border-radius: 5px;
}

.newsi a {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.666);
}

#progs,
#btn1,
#btn2 {
  font-size: 1.3rem;
  padding: 10px 15px;
  margin: 5px;
  border: none;
  background-color: white;
  box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.466);
  text-align: center;
  border-radius: 10px;
}

#btn1,
#btn2 {
  width: 50%;
  cursor: pointer;
}

#btn1:hover,
#btn2:hover {
  background-color: rgba(229, 229, 229, 0.33);
}


.head {
  font-size: clamp(1vw, 1rem, 3vw)
}

@media (max-width: 1150px) {
  .ausgabe {
    flex-direction: column;
    align-items: center;
  }

  .links {
    display: none;
  }
}